<template>
  <div id="actived">
    <ul>
      <li v-for='good in lists' :key='good'>
        <div class='lhead' @click='toggleSlide' :id='good[2].id'>{{good[0].lname}} <span>▼</span></div>
        <ul :class='good[2].id'>
          <li v-for="content in good[1].lcontent" :key="content" >
            {{content}}
          </li>
        </ul>
      </li>
    </ul>
   
  </div>
</template>

<script>
import goods from '../assets/data/list.json'

export default {
  name: 'activedlist',
  data () {
    return {
      lists: goods
    }
  },
  methods: {
    toggleSlide: function (e) {
      var target = 'ul.' + e.target.id
      $(target).stop().animate({'height': 'toggle', 'opacity': 'toggle'}, 1000)
    }
  }
}
</script>

<style>
#actived ul li {width:198px; background:#ff6a83; float:left; margin:15px;}
#actived ul li .lhead {position:relative; padding:8px 10px; cursor: pointer; color:#ffffff; font-weight: 500}
#actived ul li .lhead span{position:absolute; right:15px}
#actived ul li ul {display: none}
#actived ul li ul li {width:180px; background: #ffffff; padding:6px 8px; margin:0; border:1px solid #707070; border-top:0; }

</style>



